.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.intoImg {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
    opacity: 0.2;
}

.mask ::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.8;

}

.hero {
    height: 10vh;
    width: 100%;
}

.hero .content {
    position: relative;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero .content h3 {
    font-size: 4rem;
    padding-left: 100px;
    letter-spacing: 2px;
    margin-bottom: 20px;
    text-align: left;
    color: white;
}

.helloText {
    font-size: 30px;
    text-align: left;
    padding-left: 100px;
    color: white;
    letter-spacing: 4px;
    margin-bottom: 20px;

}

.helloText {
    position: relative;
}
.btn button{

    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    background-color:tomato;
    color: white;
    border: 1px solid;
    font-weight: 600;
    cursor: pointer;
    border: 0px;
    width: 150px;
}

.btnLight button{
    background: rgb(65, 64, 64);
    padding: 12px 32px;
    color: white;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    align-items: center;
    margin-top: 15px;
    width: 150px;
}

.btn button :hover {
    background-color: pink;
    transition: 0.3;
}

.btn-light:hover {
    background-color: gray;
}

