.designationText {

    text-align: left;
    padding-left: 100px;
    word-spacing: 10px;
    letter-spacing: 2px;
    color: rgb(161, 224, 224);
    margin-bottom: 10px;
}

.hero .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.content .btn {
    margin: 1rem 0.2rem;

}

.content .btn :hover {
    background:rgb(243, 106, 129);
}

.btn-light {
    margin: 1rem 0.2rem;
    width: auto;
    background: rgb(41, 40, 40);
    margin-top: 10px;
}

.buttons {
    display: flex;
    justify-content: flex-start;
    gap: 15px;
    padding-left: 100px;
   

}

.content {
    position: relative;
    height: 150vh;
    /* margin-top: -400px; */
}

@media screen and (max-width:450px) {
    .buttons {
        display: grid;
        grid-template-columns: repeat(1, auto);
        row-gap: 0px;
        padding-left: 50px;
    }
    /* .btnLight button {
        margin-bottom: 200px;
    } */

    .helloText {
        padding-left: 50px;
    }

    .nameheading {
        margin-left: -50px;
        width: 70%;
        margin-right: 20px;
        color: white;
    }

    .designationText {
        padding-left: 50px;
    }

    @keyframes typing {

        20%,
        30% {
            left: calc(100% + 30px);
        }

        50% {
            left: 0;

        }
    }
}

@media screen and (max-width :200px) {
    .nameheading {
        font-size: 10px;
    }
}

